.offer-widget {
    position: relative;
    padding: 30px 60px 30px 30px;
    border-radius: 4px;
    border: 2px solid;

    &_news {
        border-color: #9c00ce;

        .offer-widget__btn {
            background-color: #9c00ce; }

        .offer-widget__icon {
            color: rgba(#9c00ce, .1); } }

    &__text {
        margin-bottom: 20px;
        font-size: $mini-fz;
        color: $dark-c; }

    &__btn {
        color: $light-c;
        letter-spacing: 0.04em; }

    &__icon {
        position: absolute;
        bottom: 15px;
        right: 20px;
        font-size: 0;

        &::before {
            font-size: 90px; } } }
