.index__last-news {
    padding: 20px;
    background-color: $light-c;
    @extend %border; }

.last-news {
    &__section-title {
        margin-bottom: 15px;
        font-size: $big-fz; }

    &__link {
        display: inline-block;
        margin-top: 20px;
        color: #000;
        font-size: 14px;
        letter-spacing: 0.08em;
        text-decoration: underline;

        &:hover,
        &:focus {
            text-decoration: none; } } }

.last-news-list {
    margin: 0;
    padding: 0; }

.last-news-item {

    &:not(:last-of-type) {
        margin-bottom: 20px; }

    &__link {
        display: flex;
        align-items: flex-start;
        color: $dark-c;
        text-decoration: none;

        &:hover,
        &:focus {
            .last-news-item__title {
                text-decoration: underline; } } }

    &__img {
        width: 110px;
        margin-right: 20px; }

    &__title {
        font-size: $mini-fz;
        line-height: $mini-lh;
        font-family: $base-f;
        font-weight: normal;
        margin-bottom: 10px; }

    &__date {
        font-size: $micro-fz;
        color: $base-gray;
        text-transform: uppercase; } }

.news-list-item {
    display: flex;
    align-items: stretch;
    min-height: 200px;
    background-color: $light-c;
    color: $dark-c;
    text-decoration: none;
    @extend %border;

    &:not(:last-of-type) {
        margin-bottom: 20px; }

    &__img {
        display: block;
        width: 100%; }

    &__img-wrap {
        position: relative;
        width: 310px;
        flex-shrink: 0;
        background-size: cover;
        background-position: 50% 50%; }

    &__text-wrap {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        padding: 20px; }

    &__title {
        display: block;
        margin-bottom: 20px;
        font-size: $medium-fz;
        color: $dark-c;
        text-decoration: none;

        &:hover {
            color: $primary-blue; } }

    &__text {
        margin-bottom: 40px;
        font-size: $mini-fz; }

    &__date {
        margin-top: auto;
        font-size: $mini-fz;
        font-family: $additional-f;
        letter-spacing: 0.08em;
        color: #7a7a7a; } }

.news-item {
    margin-bottom: 40px;
    padding-left: 20px;

    &__cover-wrap {
        position: relative;

        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 2;
            background: linear-gradient(rgba(0, 51, 104, 0) 0%, rgba(8, 44, 86, 0.02) 77.39%, rgba(9, 44, 85, 0.25) 91.63%, rgba(23, 33, 54, 0.8) 100%);
            opacity: .6; } }

    &__cover {
        display: block;
        position: relative;
        z-index: 1; }

    &__img-figcaption {
        position: absolute;
        bottom: 15px;
        left: 30px;
        right: 30px;
        z-index: 3;
        font-size: 0; }

    &__img-description {
        margin-right: 20px; }

    &__img-description,
    &__img-author {
        display: inline-block;
        width: 240px;
        font-size: 14px;
        letter-spacing: 0.08em;
        line-height: 20px;
        color: #dde2e5; }

    &__content {
        margin-top: 20px;
        margin-left: 110px; }

    &__date {}

    &__title {
        margin-bottom: 30px;
        font-size: $huge-fz;
        font-family: $bold-f; }

    &__lead {
        margin-bottom: 30px;
        font-size: 21px;
        font-family: $additional-f;
        line-height: $mini-lh; }

    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 60px; }

    &__author {
        font-size: 18px; } }
